.slider_item {
  border-radius: 10px;
  position: relative;
  padding-top: 20px;
  padding-left: 20px;

  @media screen and (max-width: 576px) {
    height: 100%;
  }

  @media screen and (min-width: 768px) {
    padding: 40px;
  }
}

.slider_item.slide1 {
  background: url(../../images/Delivery/ukr.jpg) no-repeat;
  background-size: cover;
}

.slider_item.slide2 {
  background: url(../../images/Delivery/pol.jpg) no-repeat;
  background-size: cover;
}

.slider_item:before {
  content: "";
  position: absolute;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.4);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.title {
  font-weight: 400;
  font-size: 24px;
  color: #ffffff;
  padding: 20px;
  cursor: pointer;
  white-space: nowrap;
  height: 100%;
  margin: 0 0 32px;
  transform: rotate(0deg);

  @media screen and (min-width: 768px) {
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    font-size: 38px;
    margin-right: 32px;
  }
}

.content {
  width: 90%;
  background: rgb(213 213 213 / 20%);
  color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  z-index: 2;
  margin: 0;
  position: relative;
  bottom: 20px;

  @media screen and (min-width: 768px) {
    bottom: 20px;
  }
}

.wide{
  max-width: 497px;
  //height: 433px;
  //max-height: 496px;

  @media screen and (min-width: 768px){
    max-width: 100%;
    width: 575px;
    //max-height: 100%;
    height: 685px;
  }

  @media screen and (min-width: 992px) {
    width: 495px;
  }

  @media screen and (min-width: 1200px) {
    width: 593px;
  }
}

.tight {
  max-width: 497px;
  height: 76px;

  @media screen and (min-width: 768px){
    max-width: 100%;
    width: 150px;
    height: 685px;
  }
}